<template>
	<div id="store-application">
		<public-header title="商户信息"></public-header>
		<div class="content">
			<van-cell-group>
				<van-field v-model="title" label="商户名称" placeholder="店铺名称"/>
				<van-field v-model="address" label="商户地址" placeholder="店铺详细地址"/>
				<van-field v-model="mobile" label="联系电话" placeholder="店铺负责人电话"/>
				<van-field v-model="content" type="textarea" label="商户简介" />
			</van-cell-group>
			<div class="protocol">
				我已阅读并同意 <span class="c-danger" @click="agreementShow = true">《商户入驻协议》</span>
			</div>
			<div class="submit">
				<van-button @click="onSubmit" size="normal" type="danger" :block="true">提交</van-button>
			</div>
		</div>
		<agreement :show="agreementShow" :title="agreement.title" :content="agreement.value" @agreementHide="agreementHide"></agreement>
		<imessage v-if="message.type" :message = message></imessage>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Agreement from '@/components/Agreement'
	import imessage from '@/components/imessage'

	export default {
		name: "storeApplication",
		components: {
			PublicHeader,
			Agreement,
			imessage
		},
		data() {
			return {
				agreementShow: false,
				agreement: {},
				mobile: '',
				title: '',
				content: '',
				address: '',
				message: {
					type: '',
					message: '',
					description: '',
					redirect: '',
					btnText: '',
				}
			}
		},
		methods: {
			agreementHide() {
				this.agreementShow = false
			},
			onSubmit() {
				if(!this.mobile){
					this.$toast('手机号不能为空');
					return false;
				}
				var reg = /^[01][345678][0-9]{9}/;
				if(!reg.test(this.mobile)) {
					this.$toast('手机号格式错误');
					return false;
				}
				if(!this.address){
					this.$toast('商户地址不能为空');
					return false;
				}
				if(!this.title){
					this.$toast('商户名称不能为空');
					return false;
				}
				this.util.request({
					url: 'wmall/store/settle/store',
					method: 'POST',
					data: {
						telephone: this.mobile,
						address: this.address,
						content: this.content,
						title: this.title
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno == -1) {
						this.$toast(result.message);
						return false;
					}
					this.onLoad();
				})
			},
			onLoad() {
				this.util.request({
					url: 'wmall/store/settle/store',
				}).then((res) => {
					let result = res.data.message;
					if(result.errno == -1000) {
						this.util.$toast(result.message, this.util.getUrl({path: '/pages/store/settle'}), 1000, 'replace');
					} else if(result.errno == -1001) {
						this.message = {
							type: 'info',
							message: result.message.message,
							redirect: this.util.getUrl({path: '/pages/member/mine'}),
							btnText: '确定',
						} 
					} else if(result.errno == -1) {
						this.message = {
							type: 'error',
							message: result.message,
							redirect: this.util.getUrl({path: '/pages/member/mine'}),
							btnText: '返回',
						} 
					} else if(result.errno == 0) {
						//manager
						this.util.$toast(result.message.message, this.util.getUrl({path: '/pages/home/index'}), 1000, 'replace');
					} else {
						this.agreement = result.message.agreement;
					}
				})
			}

		},
		mounted() {
			this.onLoad();
		}
		
	}
</script>

<style scoped>
	#store-application .protocol{
		margin-top:10px;
		margin-right:10px;
		text-align: right;
		font-size:14px;
		color:#B1B1B1;
	}
	#store-application .submit{
		margin-top: 10px;
		padding: 0 15px;
	}
</style>


